.pages-for-services {

  .header {
    background-color: #030B16;
    margin-bottom: 33px;
  }

  .breadcrumbs {
    align-items: center;
    font-size: 12px;
    margin-bottom: 55px;

    .crumb {
      display: flex;
      align-items: center;
    }

    .link {
      color: #0C1C32;
      transition: .5s;

      &:hover {
        color: $color_blue;
      }
    }

    .separation-dot {
      margin: 0 5px 0;
    }
  }

  .title {
    margin-bottom: 50px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 28px;
    line-height: 158.6%;
  }

  .main_image {
    width: 100%;
    margin-bottom: 40px;

    .image {
      width: 100%;
    }
  }

  .text-under-image {
    font-size: 18px;
    line-height: 181.69%;
    margin-bottom: 60px;
  }

  .answers {
    display: flex;
    justify-content: space-between;
    margin-bottom: 115px;

    .text {
      line-height: 160%;
    }
  }

  .value {
    display: flex;
    flex-direction: column;
    width: 55%;

    .list {
      flex-wrap: wrap;
    }

    .item {
      width: 47%;

      &:not(:last-child) {
        margin-bottom: 25px;
      }

      &:nth-child(1) {
      margin-right: auto;
      }

      &:nth-child(2) {
      }

      &:nth-child(3) {
        margin-right: auto;
      }

      &:nth-child(5) {
      }
    }

    .subtitle {
      margin-bottom: 33px;
    }

    .number {
      @include font_Furore;
      font-size: 50px;
      line-height: 158.6%;
      color: $color_blue;
      margin-bottom: 5px;
    }
  }

  .time {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40%;
    padding: 55px 22px 75px 35px;
    background-color: $color_blue;
    color: $color_white;
    margin-top: 15px;

    .container {
      display: flex;
      flex-wrap: wrap;
    }

    .indicators {
      width: 47%;

      &:nth-child(1) {
      margin-right: auto;
      margin-bottom: 25px;
      }

      &:nth-child(2) {
        margin-bottom: 25px;
      }
    }

    .description {
      font-weight: 600;
      font-size: 20px;
      line-height: 159.19%;
    }

    .number {
      @include font_Furore;
      font-size: 36px;
      line-height: 136.6%;
      letter-spacing: 0.05em;
      margin-bottom: 10px;
    }

    .name {
      font-weight: 500;
      font-size: 14px;
      line-height: 162.19%;
      color: rgba(255, 255, 255, 0.7);
    }

    .list {
      flex-direction: column;
      margin-left: 20px;
      list-style-type: disc;
    }

    .item {
      margin-bottom: 20px;
    }
  }

  .make {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    background: url('../img/question.png') no-repeat left center;
    background-size: contain;
    margin-bottom: 110px;

    .content {
      display: flex;
      flex-direction: column;
      width: 73%;
    }

    .subtitle {
      margin-bottom: 27px;
    }

    .text {
      line-height: 160%;

      &:not(:last-of-type) {
        margin-bottom: 22px;
      }

      &:last-of-type {
        margin-bottom: 70px;
      }
    }

    .list {
      flex-direction: column;
      margin-bottom: 8px;
    }

    .item {
      position: relative;
      margin-bottom: 12px;
      margin-left: 12px;

      &::before {
        content: '';
        position: absolute;
        top: 8px;
        left: -10px;
        width: 0;
        height: 0;
        border-radius: 50%;
        border: 1px solid $color_black;
      }
    }

    .explanation {
      display: flex;
      align-items: center;
      width: 100%;
      padding: 28px 20px 34px 39px;
      border: 2px solid #2482FF;
      border-radius: 1px;

      .icon {
        flex-shrink: 0;
        margin-right: 20px;
      }

      .text {
        margin: 0;
        font-weight: 600;
        font-size: 18px;
      }
    }
  }

  .benefit {
    display: flex;
    flex-direction: column;

    .subtitle {
      margin-bottom: 15px;
    }

    .text {
      line-height: 160%;
      margin-bottom: 45px;
    }

    .list {
      justify-content: space-between;
    }

    .item {
      display: flex;
      flex-direction: column;
      position: relative;
      overflow: hidden;
      width: 32%;
      min-height: 283px;
      padding: 70px 30px 20px 80px;
      background-color: $color_blue;
      color: $color_white;

      &:before {
        @include font_Furore;
        position: absolute;
        align-self: center;
        bottom: -7px;
        margin-left: -25px;
        font-size: 38.7051px;
        letter-spacing: 0.05em;
        color: transparent;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: rgba(255, 255, 255, 0.5);
        text-transform: uppercase;
        white-space: nowrap;
      }

      &:nth-child(1) {
        &:before {
          content: 'Прогнозная аналитика';
        }
      }

      &:nth-child(2) {
        &:before {
          content: 'Цифровизация';
        }
      }

      &:nth-child(3) {
        &:before {
          content: 'Deep learning';
        }
      }

      .text {
        position: relative;
        margin: 0;
        font-weight: 600;
        font-size: 18px;
        line-height: 166.19%;

        &:before {
          content: '';
          position: absolute;
          top: 9px;
          left: -35px;
          width: 8px;
          height: 8px;
          background-color: #ffffff;
          border: 4px solid #509bff;
        }
      }
    }
  }

  .team {
    padding-top: 100px;
    padding-bottom: 70px;
    margin-bottom: 110px;
    background: #030b16 url('../img/team_bg.png') no-repeat center center;
    background-size: cover;
    color: $color_white;

    .list {
      width: 100%;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    .item {
      width: 32%;
      padding-right: 50px;
      margin-bottom: 30px;
    }

    .subtitle {
      margin-bottom: 35px;
    }

    .number {
      @include font_Furore;
      font-size: 50px;
      letter-spacing: 0.05em;
      color: transparent;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
      text-transform: uppercase;
      margin-bottom: 15px;
    }

    .text {
      font-size: 14px;
      line-height: 180.69%;
    }
  }

  .effects {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 110px;

    .list {
      flex-wrap: wrap;
    }

    .item {
      display: flex;
      align-items: center;
      min-height: 167px;
      width: 50%;
      padding: 0 75px 0 84px;
      border: 1px solid rgba(0, 0, 0, 0.1);

      &:nth-child(1) {
        border-right-color: transparent;
      }

      &:nth-child(2) {
      }

      &:nth-child(3) {
        border-right-color: transparent;
        border-top-color: transparent;
      }

      &:nth-child(4) {
        border-top-color: transparent;
      }
    }

    .subtitle {
      margin-bottom: 33px;

      .text {
        font-size: 16px;
        font-weight: normal;
      }
    }

    .number {
      @include font_Furore;
      font-size: 50px;
      line-height: 158.6%;
      color: $color_blue;
      margin-right: 35px;
    }

    .text {
      line-height: 164.69%;
    }
  }

  .experience {
    margin-bottom: 120px;

    .subtitle {
      font-weight: bold;
      font-size: 28px;
      line-height: 158.6%;
      margin-bottom: 40px;
    }

    .text {
      line-height: 160%;
    }

    .container {

      .subtitle {
        font-weight: bold;
        font-size: 22px;
        line-height: 174.1%;
        margin-bottom: 10px;
      }
    }

    .description {
      font-size: 18px;
      line-height: 197.69%;
      margin-bottom: 40px;
    }

    .list {
      flex-direction: row;
      margin-bottom: 34px;
    }

    .separator_line {
      height: 64px;
      width: 1px;
      margin: 0 26px 0;
      background-color: #e5e5e5;
    }


    .item {
      padding: 0;
      margin: 0;

      &:before {
        display: none;
      }

      &:first-child {
        width: 18%;
      }

      &:nth-child(3) {
        width: 25%;
      }

      .number {
        @include font_Furore;
        font-size: 36px;
        line-height: 136.6%;
        letter-spacing: 0.05em;
        color: $color_blue;
        opacity: 0.9;
        margin-bottom: 6px;
      }

      .text {
        font-weight: 500;
        font-size: 14px;
        line-height: 149.69%;
        opacity: 0.7;
        margin: 0;
      }
    }

    &__slider {
      margin: 0;

      &_container {
        flex-shrink: 0;
      }

      &_item {
        .button_bg_blue {
          padding: 0 55px 0;
        }
      }
    }
  }

  .contacts {
    width: 100%;
    margin-bottom: 110px;

    .text {
      line-height: 160%;
    }

    .form {
      max-width: 100%;
      width: 100%;
      margin: 0;
      padding: 67px 65px 90px;
      background: $color_blue url('../img/form_bg.png') no-repeat center center;
      background-size: cover;

      &__title {
        font-size: 26px;
      }

      .row {
        display: flex;
        justify-content: space-between;
      }

      .input, .submit {
        width: 32%;
        height: 70px;
        margin-bottom: 27px;
      }
    }
  }

  .all-services {
    margin-bottom: 155px;

    .subtitle {
      margin-bottom: 40px;
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
    }

    .item {
      display: flex;
      flex-direction: column;
      width: 33.3333%;
      background-color: #ffffff;
      border: 1px solid rgba(0, 0, 0, 0.1);
      transition: .5s;

      &:hover {
        background-color: $color_blue;
        border-color: $color_blue;
      }

      &:nth-child(2) {
        border-left-width: 0;
        border-right-width: 0;
      }

      &:nth-child(4) {
        border-top-width: 0;
      }

      &:nth-child(5) {
        border-right-width: 0;
        border-left-width: 0;
        border-top-width: 0;
      }

      &:nth-child(6) {
        border-top-width: 0;
      }

      .link {
        display: flex;
        align-items: center;
        min-height: 212px;
        width: 100%;
        padding: 0 75px 0 80px;
        font-weight: 600;
        font-size: 18px;
        line-height: 157.19%;
        color:  rgba(12, 28, 50, 0.7);
        transition: .5s;

        &:hover {
          color: #ffffff;
        }
      }

      &.active {
        background-color: $color_blue;
        border-color: $color_blue;

        .link {
          color: #ffffff;
          cursor: default;
          pointer-events: none;
        }
      }

    }
  }

  &.digital_doubles {
    .team {
      min-height: 597px;
      padding-top: 255px;
      margin-top: -145px;
    }
  }

  &.digital_transformation {
    .team {

      .list {
        justify-content: flex-start;
      }
    }
  }

  &.road_map {
    .make {
      .subtitle {
          margin-bottom: 18px;
      }

      .text {
        &:not(:last-of-type) {
          margin-bottom: 15px;
        }

        &:last-of-type {
          margin-bottom: 15px;
        }
      }
    }
  }

  &.training {

    .answers {
      margin-bottom: 80px;
    }

    .value {
      width: 100%;

      .subtitle {
        margin-bottom: 19px;
        &+.text {
          margin-bottom: 45px;
        }
      }

      .list {
        justify-content: space-between;
      }

      .item {
        width: 24%;

        &:not(:last-child) {
          margin-bottom: 33px;
        }

        &:nth-child(1) {
        margin-right: 0;
        }

        &:nth-child(2) {
          margin-right: 0;
        }

        &:nth-child(3) {
          margin-right: 0;
        }

        &:nth-child(5) {
        }
      }
    }

    .benefit {
      margin-bottom: 115px;

      .subtitle {
        margin-bottom: 25px;
      }

      .name {
        font-size: 20px;
        margin-bottom: 30px;
      }

      .list {
        flex-wrap: wrap;
      }

      .item {
        width: 23%;
        min-height: 180px;
        padding: 45px 30px 20px 56px;

        &:before {
          display: none;
        }

        .text {
          font-size: 14px;
          font-weight: 600;
          &:before {
            top: 4px;
          }
        }
      }
    }
  }

  &.privacy_policy {
    .block {
      display: flex;
      flex-direction: column;
      margin-bottom: 50px;

      &:last-child {
        margin-bottom: 150px;
      }

      .subtitle {
        margin-bottom: 15px;
      }

      .text {
        line-height: 160%;
        margin-bottom: 22px;
      }
    }
  }
}

