.pages-for-solutions {

  .header {
    background-color: #030B16;
    margin-bottom: 0;
  }

  .breadcrumbs {
    align-items: center;
    font-size: 12px;
    margin-bottom: 55px;

    .crumb {
      display: flex;
      align-items: center;
    }

    .link {
      color: $color_white;
      opacity: 1;
      transition: .5s;

      &:hover {
        opacity: 0.6;
      }
    }

    .separation-dot {
      margin: 0 5px 0;
    }
  }

  .title {
    color: $color_white;
    margin-bottom: 18px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 28px;
    line-height: 158.6%;
  }

  .text-under-image {
    font-size: 18px;
    line-height: 181.69%;
    color: $color_white;
    // margin-bottom: 68px;
  }

  .top-block {
    min-height: 315px;
    margin-bottom: 70px;
    padding-bottom: 40px;
    background-color: #236ac8;
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: cover;

    .wrapper {
      padding-top: 32px;
    }
  }

// make
  .tasks {
    display: flex;
    width: 100%;
    margin-bottom: 110px;

    .content {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    .subtitle {
      margin-bottom: 27px;
    }

    .functions {
      font-weight: bold;
      font-size: 22px;
      line-height: 158.6%;
      opacity: 0.9;
      margin-bottom: 40px;
    }

    .text {
      line-height: 160%;

      &:not(:last-of-type) {
        margin-bottom: 60px;
      }

      &:last-of-type {
        margin-bottom: 53px;
      }
    }
  }

  .explanation {
    display: flex;
    align-items: center;
    min-height: 158px;
    width: 100%;
    padding: 28px 20px 34px 60px;
    border: 2px solid #2482FF;
    border-radius: 1px;

    .icon {
      flex-shrink: 0;
      margin-right: 20px;
    }

    .text {
      margin: 0;
      font-weight: 600;
      font-size: 18px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  .customers {
    display: flex;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    // background-color: rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;

    .container {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      min-height: 230px;
      width: 20%;
      padding: 58px 30px 10px;
      background-color: $color_white;


      &:not(:last-child) {
        border-right: 1px solid rgba(0, 0, 0, 0.1);
      }

      .icon {
        height: 54px;
        margin-bottom: 19px;
      }

      .text {
        margin: 0;
      }
    }
  }

  // .benefit
  .technology {
    display: flex;
    flex-direction: column;

    .subtitle {
      margin-bottom: 15px;
    }

    .text {
      line-height: 160%;
      margin-bottom: 45px;
    }

    .list {
      background: $color_blue url('../img/form_bg.png') no-repeat center center;
      background-size: cover;
    }

    .item {
      display: flex;
      flex-direction: column;
      width: 33.3333%;
      min-height: 266px;
      padding: 40px 17px 20px 43px;
      color: $color_white;

      &:nth-child(2) {
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        border-left: 1px solid rgba(255, 255, 255, 0.1);
      }


      .number {
        @include font_Furore;
        font-size: 50px;
        letter-spacing: 0.05em;
        color: transparent;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
        text-transform: uppercase;
        margin-bottom: 15px;
      }

      .name {
        font-weight: bold;
        font-size: 18px;
        line-height: 197.69%;
        margin-bottom: 12px;
      }

      .text {
        font-size: 16px;
        line-height: 149.69%;
        opacity: 0.8;
        margin: 0;
      }
    }
  }

  // .team
  .scenarios {
    min-height: 597px;
    padding-top: 240px;
    margin-top: -130px;
    margin-bottom: 120px;
    background: #030b16 url('../img/team_bg.png') no-repeat center center;
    background-size: cover;
    color: $color_white;

    .list {
      width: 100%;
      flex-direction: column;
      padding-left: 35px;
      margin-bottom: 96px;
    }

    .item {
      width: 100%;
      margin-bottom: 38px;
    }

    .subtitle {
      margin-bottom: 40px;
    }

    .number {
      @include font_Furore;
      font-size: 50px;
      letter-spacing: 0.05em;
      color: transparent;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
      text-transform: uppercase;
      margin-bottom: 15px;
    }

    .text {
      font-size: 16px;
      line-height: 167.69%;

      &:first-child {
        position: relative;
        font-weight: bold;
        line-height: 206.69%;

        &:before {
          content: '';
          position: absolute;
          top: 8px;
          left: -35px;
          width: 8px;
          height: 8px;
          background-color: #ffffff;
          border: 4px solid #509bff;
        }
      }
    }
  }

  .experience {
    margin-bottom: 112px;

    .subtitle {
      font-weight: bold;
      font-size: 28px;
      line-height: 158.6%;
      margin-bottom: 40px;
    }

    .text {
      line-height: 160%;
    }

    .container {

      .subtitle {
        font-weight: bold;
        font-size: 22px;
        line-height: 174.1%;
        margin-bottom: 10px;
      }
    }

    .description {
      font-size: 18px;
      line-height: 197.69%;
      margin-bottom: 40px;
    }

    .list {
      flex-direction: row;
      margin-bottom: 34px;
    }

    .separator_line {
      height: 64px;
      width: 1px;
      margin: 0 26px 0;
      background-color: #e5e5e5;
    }


    .item {
      padding: 0;
      margin: 0;

      &:before {
        display: none;
      }

      &:first-child {
        width: 18%;
      }

      &:nth-child(3) {
        width: 25%;
      }

      .number {
        @include font_Furore;
        font-size: 36px;
        line-height: 136.6%;
        letter-spacing: 0.05em;
        color: $color_blue;
        opacity: 0.9;
        margin-bottom: 6px;
      }

      .text {
        font-weight: 500;
        font-size: 14px;
        line-height: 149.69%;
        opacity: 0.7;
        margin: 0;
      }
    }

    &__slider {
      margin: 0;

      &_container {
        flex-shrink: 0;
      }

      &_item {
        .button_bg_blue {
          padding: 0 55px 0;
        }
      }
    }
  }

  .effects {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 110px;

    .list {
      flex-wrap: wrap;
    }

    .item {
      display: flex;
      align-items: center;
      min-height: 167px;
      width: 50%;
      padding: 0 75px 0 84px;
      border: 1px solid rgba(0, 0, 0, 0.1);

      &:nth-child(1) {
        border-right-color: transparent;
        border-bottom-color: transparent;
      }

      &:nth-child(2) {
        border-bottom-color: transparent;
      }

      &:nth-child(3) {
        border-right-color: transparent;
      }

      &:nth-child(4) {

      }
    }

    .subtitle {
      margin-bottom: 33px;
    }

    .number {
      @include font_Furore;
      font-size: 50px;
      line-height: 158.6%;
      color: $color_blue;
      margin-right: 35px;
    }

    .text {
      line-height: 164.69%;
    }
  }

  .contacts {
    width: 100%;
    margin-bottom: 80px;

    .text {
      line-height: 160%;
    }

    .form {
      max-width: 100%;
      width: 100%;
