      min-height: 145px;
      width: 33.3333%;
      border: 1px solid rgba(0, 0, 0, 0.1);
      font-weight: 600;
      font-size: 22px;
      line-height: 157.19%;

      &:first-child {
        border-right-color: transparent;

        .icon {
          margin-right: 20px;
        }
      }

      &:last-child {
        border-left-color: transparent;

        .icon {
          margin-left: 20px;
        }
      }
    }

    .container {
      display: flex;
      flex-direction: column;
    }

    .link {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      opacity: 0.6;

      &:hover {

        .icon {
          fill: $color_blue;
        }

        .text {
          color: $color_blue;
        }
      }

      .icon {
        fill: $color_black;
        transition: .5s;
      }
    }

    .color_blue {
      width: 85%;
      text-align: center;
    }

    .prev {
      justify-content: flex-start;
      padding-left: 70px;

      .container {
        align-items: flex-start;
      }

      .text {
        text-align: left;
      }
    }

    .next {
      justify-content: flex-end;
      padding-right: 70px;

      .container {
        align-items: flex-end;
      }

      .text {
        text-align: right;
      }
    }

    .text {
      font-weight: 600;
      font-size: 16px;
      line-height: 157.19%;
      color: $color_black;
      transition: .5s;
    }
  }

